.result-panel {
  background-color: #2a7fffd1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  padding: 80px 20px 20px 20px;

  &.web {
    padding: 0px 20px 20px 20px;
    height: 800px;

    button {
      height: 40px;
    }
  }

  .icon0,
  .icon1,
  .icon2,
  .icon3 {
    margin-bottom: 10px;
  }

  .img {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  button {
    background-color: white;
    min-width: 300px;
    margin-bottom: 10px;
    height: 50px;

    &:first-child {
      margin-top: 10px;
    }

    span {
      color: black;
    }
  }
}
